import React from "react";
import ReactModal from "react-modal";

const legislatures = [
  "87(R) - 2021",
  "86(R) - 2019",
  "85(1) - 2017",
  "85(R) - 2017",
  "84(R) - 2015",
  "83(R) - 2013",
];

export default function SearchByModal({ state, showHideModal }) {
  return (
    <ReactModal
      isOpen={state.showModal}
      overlayClassName="Overlay"
      className="Modal br3"
    >
      <div className="center pa4 primaryColor">
        <form>
          <p className="f4">Search by</p>
          <div className="di ma3">
            <input type="radio" name="searchBy" id="wordRadio" defaultChecked />
            <label htmlFor="wordRadio" className="mh2">
              Word/Phrase
            </label>
          </div>
          <div className="di ma3">
            <input type="radio" name="searchBy" id="billRadio" />
            <label htmlFor="billRadio" className="mh2">
              Bill Number
            </label>
          </div>
          <div className="pa3">
            <label htmlFor="legislature">Legislature:</label>
            <select name="legislature" id="">
              {legislatures.map((legislature, i) => (
                <option value={legislature} key={i}>
                  {legislature}
                </option>
              ))}
            </select>
          </div>
          <button
            className="input-reset br3 primaryColor mt4 pv2 ph3 ba bw1 b--blue bg-white"
            onClick={() => showHideModal(false)}
          >
            Search
          </button>
        </form>
      </div>
    </ReactModal>
  );
}
