import React, { Component } from "react";
import logo from "./logo.jpg";
import "./NavBar.css";

class NavBar extends Component {
  constructor() {
    super();
    this.state = {
      logoHeight: window.pageYOffset > 0 ? "90px" : "125px",
      background:
        window.pageYOffset > 50
          ? "bg-white primaryColor"
          : "primaryBackground white",
    };
  }

  componentDidMount() {
    console.log(window.pageYOffset);
    window.addEventListener("scroll", () => {
      this.setState({
        logoHeight: window.pageYOffset > 50 ? "90px" : "125px",
        background:
          window.pageYOffset > 50
            ? "bg-white-90 primaryColor"
            : "primaryBackground white",
      });
    });
  }
  render() {
    return (
      <div
        className={`navBar flex justify-between shadow-1 ${this.state.background}`}
      >
        <div className="flex">
          <div style={{ width: "140px" }} className="tc bg-white ">
            <img
              src={logo}
              alt="logo"
              height={this.state.logoHeight}
              width="auto"
              className="ph2 bg-white"
            />
          </div>
          <div className="ml2">
            <p className="f2 mb0 mt3">Texas Legislature Online</p>
            <p className="f5 mt0">87th Legislature Regular Session</p>
          </div>
        </div>
        <div className="ma2 mr3">
          <div className="flex flex-column">
            <div className="f7 self-end br-pill secondaryBackground white pv1 ph2">
              <span>Help | </span>
              <span>FAQ | </span>
              <span>Site Map | </span>
              <span>Contact | </span>
              <a className="white" href="https://capitol.texas.gov">
                Old Site
              </a>
            </div>
            <div className="flex mt4">
              <span className="mh2 b">Home</span>
              <span className="mh2 pointer grow">My TLO</span>
              <span className="mh2 pointer grow">House</span>
              <span className="mh2 pointer grow">Senate</span>
              <span className="mh2 pointer grow">Legislation</span>
              <span className="mh2 pointer grow">Search</span>
              <span className="mh2 pointer grow">Committees</span>
              <span className="mh2 pointer grow">Calendars</span>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default NavBar;
